import { Component, OnInit } from '@angular/core';
import * as d3 from 'd3';
import { HttpService } from '../http.service';

@Component({
  selector: 'app-pie',
  templateUrl: './pie.component.html',
  styleUrls: ['./pie.component.css']
})
export class PieComponent implements OnInit {
  // private data = [
  //   {"Framework": "Vue", "Stars": "166443", "Released": "2014"},
  //   {"Framework": "React", "Stars": "150793", "Released": "2013"},
  //   {"Framework": "Angular", "Stars": "62342", "Released": "2016"},
  //   {"Framework": "Backbone", "Stars": "27647", "Released": "2010"},
  //   {"Framework": "Ember", "Stars": "21471", "Released": "2011"},
  // ];
  private data:any;
  private svg: any;
  private margin = 50;
  private width = 750;
  private height = 600;
  // The radius of the pie chart is half the smallest side
  private radius = Math.min(this.width, this.height) / 2 - this.margin;
  private colors: any;

  constructor(private HttpService: HttpService) {}

  ngOnInit(): void {
    this.createSvg();
    this.HttpService.sendGetRequest('/poketypes').subscribe((res: any) => {
      this.data = res;
      this.createColors(res);
      this.drawChart(res);
    });
   
  }

  private createSvg(): void {
      this.svg = d3.select("figure#pie")
      .append("svg")
      .attr("width", this.width)
      .attr("height", this.height)
      .append("g")
      .attr(
        "transform",
        "translate(" + this.width / 2 + "," + this.height / 2 + ")"
      );
  }

  private createColors(data: any[]): void {
      this.colors = d3.scaleOrdinal()
      .domain(data.map(d => d.count.toString()))
      .range(["#3B9950", "#5A5979", "#b1b12b", "#EA1369", "#EF6138", 
      "#FD4B5A", "#6F808E", "#906790", "#28C950", "#6E491F","#BEE7F5", "#C899A7", 
      "#9B69D9", "#F81C91", "#8B3E21", "#43BD94", "#86A8FC" ]);
  }

  private drawChart(data: any[]): void {
      // Compute the position of each group on the pie:
      const pie = d3.pie<any>().value((d: any) => Number(d.count));

      // Build the pie chart
      this.svg
      .selectAll('pieces')
      .data(pie(data))
      .enter()
      .append('path')
      .attr('d', d3.arc()
        .innerRadius(0)
        .outerRadius(this.radius)
      )
      .attr('fill', (d: any, i: any) => (this.colors(i)))
      .attr("stroke", "#121926")
      .style("stroke-width", "1px");

      // Add labels
      const labelLocation = d3.arc()
      .innerRadius(100)
      .outerRadius(this.radius);

      this.svg
      .selectAll('pieces')
      .data(pie(data))
      .enter()
      .append('text')
      .text((d: any) => d.data.type)
      .attr("transform", (d: any) => "translate(" + labelLocation.centroid(d) + ")")
      .style("text-anchor", "middle")
      .style("font-size", 15);
  }
}
